//
import Vue from 'vue';
// _vue-router@3.5.3@
import VueRouter from 'vue-router';
//
Vue.use(VueRouter);
//
import Home from '@/pages/Home';
import Search from '@/pages/Search';
import Login from '@/pages/Login';
import Register from '@/pages/Register';
import PracticeJs from '@/pages/PracticeJs';
import PracticeHtml from '@/pages/PracticeHtml';
import Others from '@/pages/Others';
import Position_absolute from '@/pages/Others/Position_absolute';
//
export default new VueRouter({
    //
    routes:[
        {
            path:"/home",
            component:Home,
            meta:{show:true}
        },
        {
            path:"/search/:keyword?",
            // path:"/search",
            component:Search,
            meta:{show:true},
            name:"search",
            //props
            // 1.,params
            // props:true
            // 2.
            // props:{a:1,b:2}
            // 3.
            // 
        },
        {
            path:"/login",
            component:Login,
            meta:{show:false}
        },
        {
            path:"/register",
            component:Register,
            meta:{show:false}

        },
        {
            path:"/practiceJs",
            component:PracticeJs,
            meta:{show:false}

        },
           {
            path:"/practiceHtml",
            component:PracticeHtml,
            meta:{show:false}

        },
         {
            path:"/others",
            component:Others,
            meta:{show:false},
             children: [
            {
                path: '/others/position_absolute',
                component:Position_absolute,
            }
                ]
        },
        ///
        {
            path:"*",
            redirect:"/home"
        },
    ]
})