import {reqCategoryList} from '@/api'
//home

//state:
const state = {
    //state{}
    categoryList:[],
};
//mutations:state
const mutations ={
    CATEGORYLIST(state, categoryList){
        state.categoryList = categoryList;
    }
};
//action:action

const actions = {
    //api
    async categoryList({commit}){
        let res = await reqCategoryList();
        if(res.code ==200){
                        commit('CATEGORYLIST',res.data);
        }
        console.log("res");
        console.log('res');
    }
};
//getters
const getters = {};

//store
export default{
    state,
    mutations,
    actions,
    getters
}